.restaurantdisc-wrapper {
    .breadcrumb-wrapper {
        ol {
            &.breadcrumb {
                padding: 0px;
                background: #fff;
                .breadcrumb-item {
                    a {
                        color: #232323;
                        text-decoration: none;
                    }
                    &.active {
                        color: #c1c1c1;
                    }
                }
            }
        }
        .rsbanner-wrapper {
            height: 260px;
            overflow: hidden;
            border-radius: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            img {
                height: 260px;
                object-fit: cover;
            }
        }
    }

    .rs-name {
        h2 {
            font-size: 30px;
            color: #2e482b;
        }
        .rs-dediction {
            color: #919191;
            .rsdediction-location {
                line-height: 0.5;
            }
            .rsdediction-km {
                color: #919191;
            }
            .rs-bordertyle {
                background: #919191;
                width: 1px;
                display: inline-block;
                height: 20px;
            }
        }
        .foodtypes-wrap {
            .food-types {
                background: #f6f6f6;
                width: 36px;
                height: 36px;
                border-radius: 50px;
            }
        }
    }
    .rs-opendropdown {
        .rs-opendropdown-btn {
            background: #fff !important;
            border: none;
            box-shadow: 0px 2px 18px 0px #00000014 !important;
            border-radius: 6px;
            outline: none !important;
            padding: 15px 20px;
            height: 80px;
            position: relative;
            &:after {
                content: "";
                position: absolute;
                right: 20px;
                top: 25px;
                background-image: url("../../assets/images/dropdown-arrow.svg");
                width: 10px;
                height: 10px;
                background-size: 10px;
                background-position: center;
                background-repeat: no-repeat;
                transition: ease 0.25s;
            }
            &.active {
                &:after {
                    transform: rotate(180deg);
                }
            }
        }
        #example-collapse-text {
            position: absolute;
            top:50px;
            background: #fff;
            z-index: 1;
            width: 100%;
            border: none;
            box-shadow: 0px 17px 18px 0px #00000014 !important;
            padding: 10px 20px 10px 45px;
        }
    }
}
